// Main Header ----> Web

.header__area {
    position: absolute;
    width: 100%;
    z-index: 9;
    margin: 0 auto;
    top: 0;
    left: 0;
    right: 0;
    transition: transform 0.5s ease-out;

    padding: 24px 0;
    background-color: rgba($c-white, 0);

    @media (max-width: 767px) {
        // margin: 0 8px 16px 8px;
        padding: 16px 0;
    }

    .row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 24px;
    }

    &--logo {
        width: auto;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        position: relative;
        img {
            height: 24px;

            transition: transform ease-in 0.4s;

            &:hover {
                transform: scale(1.05);
                transition: transform ease-in 0.4s;
            }
        }
    }

    &--actions {
        width: auto;
        text-align: right;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 8px;
        @media (max-width: 1024px) {
            width: auto;
            .rev {
                height: 40px;
                padding: 8px 16px;
                font-size: 14px;
            }
        }

       

        .drawer--btn {
            border: none;
            background-color: transparent;
            width: 40px;
            height: 40px;
            align-items: center;
            justify-content: center;
            cursor: pointer;

            @media (max-width: 1340px) {
                display: flex;
            }
        }
    }

    &--menu {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 32px;
        width: 80%;
        list-style: none;
        padding: 0;
        margin: 0;

        @media (max-width: 1340px) {
            display: none;
        }

        .menu--item {
            .link {
              color: $c-white;
              font-size: clamp(14px, 3vw, 16px);
              font-weight: 500;
              display: flex;
              align-items: center;
              padding: 16px 0;
              position: relative;
              transition: 0.5s ease-in-out;
              &:hover {
                color: rgba($c-main, 1);
                transition: ease-in 0.3s;
              }

              sup {
                background-color: rgba($c-white, .2);
                padding: 0px 4px;
                border-radius: 4px;
                color: $c-white;
                font-size: 12px;
                margin-left: 8px;
                margin-top: -16px;
              }
      
              &::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
                width: 60%;
                height: 2px;
                background: $c-main;
                transform: scaleX(0);
                transform-origin: center;
                transition: ease-in 0.3s;
                border-radius: 10px;
              }
      
              &:hover::after {
                transform: scaleX(1);
              }
      
              
            }
      
            .active {
              color: rgba($c-main, 1);
              transition: ease-in 0.3s;
              &::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
                width: 60%;
                height: 2px;
                background: $c-main;
                transform: scaleX(1);
                transform-origin: left;
                transition: ease-in 0.3s;
                border-radius: 10px;
              }
            }
          }
    }

    &--top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 0 16px 0;

        @media (max-width: 991px) {
        }
        @media (max-width: 1024px) {
            flex-wrap: wrap;
            gap: 8px;
        }

        .search {
            width: 50%;
            @media (max-width: 1024px) {
                order: 3;
                width: 100%;
            }
        }

        .logo {
            width: auto;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            position: relative;
            img {
                height: 60px;
                width: 216px;
                transition: transform ease-in 0.4s;

                &:hover {
                    transform: scale(1.05);
                    transition: transform ease-in 0.4s;
                }

                @media (max-width: 991px) {
                    height: 40px;
                }

                @media (max-width: 600px) {
                    height: 36px;
                }
                @media (max-width: 590px) {
                    height: 36px;
                    width: 160px;
                }
            }
        }

        .actions {
            width: auto;
            text-align: right;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 8px;
            @media (max-width: 1024px) {
                width: auto;
            }

            .panier--btn {
                height: 40px;
                padding: 0 8px;

                @media (max-width: 500px) {
                    .text {
                        display: none;
                    }
                }
            }

            .drawer--btn {
                border: none;
                background-color: transparent;
                width: 40px;
                height: 40px;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                @media (max-width: 1024px) {
                    display: flex;
                }
            }
        }
    }

    &--bottom {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 16px;
        width: 100%;
        // padding-bottom: 8px;

        .search {
            width: 100%;
            padding-bottom: 16px;
        }

        .menu {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
            width: 100%;
            list-style: none;
            padding: 0;
            margin: 0;

            @media (max-width: 1024px) {
                display: none;
            }

            .menu--item {
                .link {
                    color: $c-text;
                    font-size: clamp(12px, 3vw, 14px);
                    font-weight: 500;
                    display: flex;
                    align-items: center;
                    padding: 16px 0;
                    text-transform: uppercase !important;
                    position: relative;
                    transition: 0.5s ease-in-out;
                    &:hover {
                        color: rgba($c-main, 1);
                        transition: ease-in 0.3s;
                    }

                    &::after {
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 2px;
                        background: $c-main;
                        transform: scaleX(0);
                        transform-origin: left;
                        transition: ease-in 0.3s;
                        border-radius: 10px;
                    }

                    &:hover::after {
                        transform: scaleX(1);
                    }
                }

                .simple {
                    color: $c-main;
                }

                .active {
                    color: rgba($c-main, 1);
                    transition: ease-in 0.3s;

                    &::after {
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 2px;
                        background: $c-main;
                        transform: scaleX(1);
                        transform-origin: left;
                        transition: ease-in 0.3s;
                        border-radius: 10px;
                    }
                }
            }
        }
    }
}



// Modifier ---
