//

.dropdown-panel {
    position: absolute;
    background-color: rgba($c-white, 1);
    top: 138px;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 24px;
    padding: 24px;
    border: 1px solid gray;
    box-shadow: 0 3px 5px hsla(0, 0%, 0%, 0.1);
    border-radius: 16px;
    transform: translateY(50px);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: 0.2s ease;
    z-index: 5;
}
.menu--item:hover > .dropdown-panel {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
    pointer-events: all;
}
