.breadcrumb__wrapper {
    position: relative;
    // background-color: $c-second;
    // background-image: linear-gradient(to left, rgba($c-second, .9) , rgba($c-second, .9) ), url('/animation.gif');
    // background-repeat: no-repeat;
    // background-size: cover;
    // background-position: center;

    position: relative;
    overflow: hidden;

    &--content {
        display: flex;
        align-content: flex-start;
        justify-content: center;
        flex-direction: column;
        padding: 16% 0 4% 0;
        @media (max-width: 991px) {
            height: auto;
            padding: 30% 0 20% 0;
        }

        h1,
        h2 {
            font-size: clamp(32px, 6vw, 56px);
            line-height: clamp(40px, 6vw, 64px);
            animation-duration: 1s;
            animation-delay: 1.5s;
            font-weight: 800;
            margin-bottom: 0;
            color: $c-main;

            span {
                // text-shadow: -1px 0 $c-second, 0 1px $c-second, 1px 0 $c-second, 0 -1px $c-second;
                color: transparent;
                -webkit-text-stroke: 1px $c-white;
            }
        }

        p {
            font-size: clamp(16px, 3vw, 20px);
            color: $c-white;
            line-height: 32px;
            font-weight: 400;
            padding-top: 8px;
            margin-bottom: 0;
            @media (max-width: 991px) {
                padding-top: 8px;
            }
        }
    }
}

.gif {
    background-color: $c-second;
    background-image: linear-gradient(
            to left,
            rgba($c-second, 0.9),
            rgba($c-second, 0.9)
        ),
        url("/animation.gif");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
