.header__area {
  position: absolute;
  width: 100%;
  z-index: 9;
  margin: 0 auto;
  top: 0;
  left: 0;
  right: 0;
  transition: transform 0.5s ease-out;
  padding: 24px 0;
  background-color: rgba(255, 255, 255, 0);
}
@media (max-width: 767px) {
  .header__area {
    padding: 16px 0;
  }
}
.header__area .row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}
.header__area--logo {
  width: auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
.header__area--logo img {
  height: 24px;
  transition: transform ease-in 0.4s;
}
.header__area--logo img:hover {
  transform: scale(1.05);
  transition: transform ease-in 0.4s;
}
.header__area--actions {
  width: auto;
  text-align: right;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 8px;
}
@media (max-width: 1024px) {
  .header__area--actions {
    width: auto;
  }
  .header__area--actions .rev {
    height: 40px;
    padding: 8px 16px;
    font-size: 14px;
  }
}
.header__area--actions .drawer--btn {
  border: none;
  background-color: transparent;
  width: 40px;
  height: 40px;
