  opacity: 1;
}
.fronty--btn--2::after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #0E102E;
  left: 0;
  top: 0;
  border-radius: 8px;
}

