.copyright {
    padding: 32px 0;
    background-color: rgba($c-second, 1);
   
    .row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 16px;
      @media (max-width: 520px) {
        justify-content: center;
        flex-direction: column;
      }
    }


    p {
        text-align: center;
        font-size: clamp(14px, 3vw, 16px);
        color: $c-white;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 4px;
        a {
          color: $c-light;
      }
       

        @media (max-width: 520px) {
          order: 2;
        }
    }

    .social {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 16px;
        @media (max-width: 520px) {
          order: 1;
        }
        a {
            text-decoration: none;
            color: $c-light;
            height: 40px;
            width: 40px;
            display: flex;
            align-items: center;
            justify-items: center;
            border-radius: 8px;
            background-color: rgba($c-light, 0.1);
            transition: background ease-in 0.4s;

            i {
                margin: 0 auto;
            }

            &:hover {
                color: $c-white;
                background-color: rgba($c-main, 1);
                transition: background ease-in 0.4s;
            }
        }
    }

    small {
        color: $c-text;

        a {
            color: $c-light;
        }

        a:last-child {
            color: $c-light;
        }
    }
}
