.hero__section {
    position: relative;
    background-image: linear-gradient(to top, rgba( $c-main, 0.9), rgba( $c-main, 0.9)), url('/animation.gif');
    background-size: cover;
    background-repeat: no-repeat;

    background-color: $c-second;
    background-image: linear-gradient(to left, rgba($c-second, .9) , rgba($c-second, .9) ), url('/animation.gif');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 100vh;

  

    &--caption {
        display: flex !important;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        gap: 32px;
        text-align: left;
        animation-duration: 1s;
        animation-delay: 2s;
        width: 70%;
        height: 100vh;

        @media (max-width: 991px) {
           
            align-items: center;
            text-align: center;
            width: 100%;
        }
        @media (max-height: 767px) {
            height: calc(85vh - 0px);
        }

        &--title {
            font-size: clamp(32px, 6vw, 56px);
            line-height: clamp(40px, 6vw, 64px);
            animation-duration: 1s;
            animation-delay: 1.5s;
            font-weight: 800;
            margin-bottom: 0;
            color: $c-main;

            span {
            //   color: transparent;
            //   -webkit-text-stroke: 1px $c-main;
            }
        }
        &--subtitle {
            font-size: clamp(18px, 4vw, 24px);
            line-height: clamp(24px, 5vw, 38px);
            font-weight: 400;
            animation-duration: 0.5s;
            animation-delay: 1.4s;
            color: $c-main;
            margin-bottom: 0;
        }
        &--actions {
            display: flex;
            align-items: center;
            justify-content: center;
            animation-duration: 1s;
            animation-delay: 2.2s;
            gap: 16px;
        }
    }
}
